  .text-content {
    width: 240px;
    height: 100%;
    border-radius: 50%;
    position: absolute;
    top: auto;
    bottom: auto;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-basis: 0;
